#EducationSection {
    padding: 2rem var(--page-padding);
    border-bottom: 0.2vh solid var(--secondary-color-darkTheme);
}

#EducationSectionGrid {
    display: grid;
    grid-template-columns: 50% auto;
    height: auto;
    column-gap: 3%;
}

@media screen and (orientation:portrait) {
    #EducationSectionGrid {
        grid-template-columns: auto;
        row-gap: 3%;
    }
    
    #EducationSection > h1 {
        text-align: center;
    }
}