.ProjectDescriptionGrid {
    display: grid;
    grid-template-columns: auto;
    padding: 1rem;
    border-radius: 0.5rem;
    box-shadow: var(--box-shadow);
}
.ProjectDescriptionGrid > div:last-child > button {
    width: 60%;
}
@media screen and (orientation:portrait) {
    .ProjectDescriptionGrid > div:last-child > button {
        width: 100%;
    }
}