#TechnologySection {
    padding: 2rem var(--page-padding);
}

#TechnologySectionGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    height: auto;
    column-gap: 2vw;
    row-gap: 2vw;
}

@media screen and (orientation:portrait) {
    #ProjectSectionGrid {
        grid-template-columns: auto;
    }

    #TechnologySection > h1 {
        text-align: center;
    }
}