#IntroSection {
    display: grid;
    grid-template-columns: 50% 50%;
    height: 85vh;
    align-content: center;
    padding: 0 var(--page-padding);
    align-items: center;
    border-bottom: 0.2vh solid var(--secondary-color-darkTheme);
}

#IntroSection > div:first-child {
    text-align: center;
}

.portfolioImg {
    width: 50%;
    height: auto;
    border: none;
    border-radius: 50%;
    box-shadow: var(--box-shadow);
}

#IntroSection > div:last-child {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto auto auto;
    align-content: center;
    align-items: center;
}

#IntroSection > div:last-child > div:first-child > h1 {
    margin-bottom: 0;
}

.about {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto auto;
    align-content: center;
}

.about > h4 {
    margin-bottom: 0;
}

.about > p {
    word-break: break-all;
}

#IntroSection > div:last-child > div:last-child {
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap: 1rem;   
    row-gap: 1rem;
}

#IntroSection > div:last-child > div:last-child {
    width: 100%;
}

@media (max-width: 1350px) {
    #IntroSection {
        grid-template-columns: 40% 60%;
    }
    .portfolioImg {
        width: 65%;
    }
}

@media (max-width: 1200px) {
    .portfolioImg {
        width: 78%;
    }
}
@media (max-width: 1050px) {
    #IntroSection {
        grid-template-columns: 30% 70%;
    }
    .portfolioImg {
        width: 90%;
    }
}
@media screen and (orientation:landscape) and (max-width: 1000px) {
    #IntroSection > div:last-child > div:last-child {
        grid-template-columns: auto auto;
    }
}
@media screen and (orientation:portrait) {
    #IntroSection {
        grid-template-columns: auto;
        grid-template-rows: auto auto;
        height: 89vh;
    }
    .portfolioImg {
        width: 30%;
    }
    #IntroSection > div:last-child {
        justify-items: center;
    }
    .about {
        justify-items: center;
    }
}
@media screen and (orientation:portrait) and (max-width: 560px) {
    #IntroSection > div:last-child > div:last-child {
        grid-template-columns: auto;
        row-gap: 0.5rem;
    }
}