.educationDescriptionGrid {
    display: grid;
    grid-template-columns: auto;
    padding: 1rem;
    border-radius: 0.5rem;
    box-shadow: var(--box-shadow);
}
.educationLogoImg {
    width: 20%;
    border: none;
    box-shadow: var(--box-shadow);
}
.educationDescriptionGrid > div:last-child > button {
    width: 60%;
}
@media screen and (orientation:portrait) {
    .educationDescriptionGrid > div:last-child > button {
        width: 100%;
    }
}