.experienceDescriptionGrid {
    display: grid;
    grid-template-columns: auto;
    padding: 1rem;
    border-radius: 0.5rem;
    box-shadow: var(--box-shadow);
}
.experienceDescriptionGrid > div:nth-child(2) {
    display: grid;
    grid-template-columns: 20% auto;
    justify-items: center;
    align-items: center;
    justify-content: start;
    column-gap: 1%;
}
.experienceLogoImg {
    width: 60%;
    border: none;
    box-shadow: var(--box-shadow);
}
.experienceDescriptionGrid > div:last-child > button {
    width: 60%;
}
@media screen and (orientation:portrait) {
    .experienceDescriptionGrid > div:last-child > button {
        width: 100%;
    }
}