.customBtn {
    background-color: var(--secondary-color-darkTheme);
    border: none;
    border-radius: 0.5rem;
    padding: 0.8rem;
    font-weight: bold;
    transition: transform 0.5s;
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 5%;
    align-items: center;
    justify-content: center;
}
.customBtn > div:last-child {
    color: var(--tertiary-color-darkTheme);
}
.customBtn:hover {
    transform: scale(1.1);
    cursor: pointer;
}