:root {
  --page-padding: 2vw;
  --font-size-extra-small: 0.8rem;
  --font-size-regular: 1rem;
  --font-size-heading: 1.8rem;
  --font-size-medium: 1.2rem;
  --font-color-dark-theme: #F5F4F2;
  --primary-color-dark-theme: #3D3D3D;
  --secondary-color-darkTheme: #C6C5C4;
  --tertiary-color-darkTheme: black;
  --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25), 0 6px 20px 0 rgba(0, 0, 0, 0.25);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--primary-color-dark-theme);
}

html {
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@media (max-width:650px) {
  :root {
    --font-size-regular: 0.95rem;
    --font-size-heading: 1.75rem;
    --font-size-medium: 1.15rem;
  }
}