nav {
    display: grid;
    grid-template-columns: auto auto auto auto;
    padding: 5vh var(--page-padding);
    justify-items: center;
    height: 4.8vh;
    align-items: center;
    border-bottom: 0.2vh solid var(--secondary-color-darkTheme);
    color: var(--font-color-dark-theme);
    font-weight: 600;
    position: sticky;
    top: 0;
    background-color: var(--primary-color-dark-theme);
}
@media screen and (orientation:portrait) {
    nav {
        padding: 3vh var(--page-padding);
    }
}