#ProjectSection {
    padding: 2rem var(--page-padding);
    border-bottom: 0.2vh solid var(--secondary-color-darkTheme);
}

#ProjectSectionGrid {
    display: grid;
    grid-template-columns: auto auto;
    height: auto;
    column-gap: 3%;
}

@media screen and (orientation:portrait) {
    #ProjectSectionGrid {
        grid-template-columns: auto;
        row-gap: 3vw;
    }

    #ProjectSection > h1 {
        text-align: center;
    }
}